.firstblock {
  padding: rem(55) 0 rem(70);

  @media (max-width: 768px) {
    padding: rem(30) 0 rem(60);
  }

  @keyframes resizeAnimate {
    0% {
      width: 90%;
    }
    50% {
      width: 100%;
    }
    100% {
      width: 90%;
    }
  }

  @media (max-width: 991px) {
    @keyframes resizeAnimate {
      0% {
        width: 70%;
      }
      50% {
        width: 100%;
      }
      100% {
        width: 70%;
      }
    }
  }

  &__title {
    position: relative;
    margin-bottom: rem(25);
    font-family: "Neometric Alt", sans-serif;
    font-size: rem(90);
    font-weight: 600;
    line-height: 1.2;

    @media (max-width: 991px) {
      font-size: rem(60);
    }

    @media (max-width: 797px) {
      font-size: rem(34);
      line-height: 1.4;
    }

    span {
      padding: rem(5);
      text-transform: uppercase;
      color: #27A3DD;
      border: rem(4) dashed #27A3DD;
    }
  }

  ._star {
    position: absolute;
    top: -20%;
    right: -3%;
    display: block;
    width: 290px;
    height: 290px;
    content: " ";
    background-repeat: no-repeat;
    background-size: contain;
    background-image: url('../img/mainblock/star.png');

    @media (max-width: 1200px) {
      display: none;
    }
  }

  &__buttons {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: rem(22);
  }

  &__button {
    cursor: pointer;
    display: flex;
    align-items: center;
    height: rem(78);
    margin-right: rem(10);
    padding-right: rem(22);
    padding-left: rem(22);
    font-family: "Neometric Alt", sans-serif;
    font-size: rem(18);
    font-weight: 600;
    color: var(--white-color);
    background: #171664;
    border: 0;
    border-radius: rem(100);
    transition: all 0.5s ease-in-out;
    white-space: nowrap;

    @media (max-width: 574px) {
      justify-content: center;
      width: 100%;
    }

    &.elem-fixed {
      z-index: 999;
      position: fixed;
      padding-right: rem(16);
      padding-left: rem(16);
      top: rem(24);
      font-size: rem(10);
      height: rem(45);
      animation: show 1s;

      img {
        height: rem(32);
      }

      @media (max-width: 574px) {
        left: 50%;
        width: calc(100% - rem(30));
        transform: translate(-50%);
      }

      @keyframes show {
        0% {
          opacity: 0;
        }

        100% {
          opacity: 1;
        }
      }
    }

    img {
      display: block;
      width: auto;
      height: rem(58);
      margin-right: rem(5);
    }

    &:hover {
      background-color: #3a397b;
    }
  }

  .arrow-left-wrap {
    display: flex;
    justify-content: right;
    width: 72%;

    @media (max-width: 574px) {
      display: none;

      &.hide-elem {
        display: none;
      }
    }

    &.hide-elem {
      display: none;
    }
  }

  .arrow-left {
    position: relative;
    display: block;
    width: 60%;
    height: rem(10);
    animation: resizeAnimate 1s linear infinite;

    &::after {
      display: block;
      width: rem(10);
      height: rem(10);
      margin: 0;
      padding: 0;
      transform: rotate(-135deg);
      content: "";
      border-top: rem(3) solid #171663;
      border-right: rem(3) solid #171663;
    }

    &::before {
      position: absolute;
      top: rem(5);
      left: 0;
      width: 100%;
      height: rem(3);
      margin-left: rem(-1);
      background: #171663;
      content: " ";
    }
  }

  &__full-video {
    overflow: hidden;
    width: 100%;
    height: rem(460);
    margin-bottom: rem(60);
    border-radius: rem(24);

    @media (max-width: 991px) {
      height: rem(260);
      margin-bottom: rem(35);
    }

    @media (max-width: 767) {
      height: rem(220);
      margin-bottom: rem(50);
      border-radius: rem(10);
    }

    @media (max-width: 574px) {
      height: rem(175);
    }

    .vjs-poster {
      background-size: cover;
    }

  }

  &__middle {

    &--cap {
      @media (min-width: 991px) {
        &.elem-fixed {
          min-height: 460px;
          background-color: #0b2530;
          margin-bottom: rem(60);
          border-radius: rem(32);
        }
      }
    }

    @media(min-width: 991px) {
      &.elem-fixed {
        position: fixed;
        bottom: rem(40);
        right: rem(40);
        width: rem(240);
        height: rem(150);
        z-index: 999;
        animation: show 1s;


        .firstblock__middle--close {
          cursor: pointer;
          position: absolute;
          z-index: 999;
          top: rem(-32);
          right: 0;
          content: "";
          width: rem(14);
          height: rem(14);
          background: no-repeat center/contain url("../img/svg/close-video.svg");
        }

        .firstblock__full-video {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          border-radius: rem(18);
        }
      }
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    width: 100%;
    flex-wrap: wrap;

    &-item {
      @include size-lg(4);
      @include size-md(4);
      @include size-sm(12);
      @include size-xs(12);

      @media (max-width: 767px) {
        &:not(:last-child) {
          margin-bottom: rem(30);
        }
      }

      &__image {
        height: rem(120);
        margin-bottom: rem(16);

        @media (max-width: 991px) {
          height: rem(110);
          margin-bottom: rem(10);
        }

        @media (max-width: 767px) {
          height: rem(100);
        }

        img {
          width: auto;
          height: 100%;
        }
      }

      &__info {
        max-width: 310px;
      }

      &__title {
        margin-bottom: rem(14);
        font-family: "Neometric Alt", sans-serif;
        font-size: rem(25);
        font-weight: 600;
        color: #171664;

        @media (max-width: 991px) {
          font-size: rem(18);
        }

        @media (max-width: 767px) {
          font-size: rem(22);
        }
      }

      &__text {
        font-size: rem(16);
        line-height: rem(19);

        @media (max-width: 991px) {
          font-size: rem(14);
          line-height: rem(17);
        }

        @media (max-width: 767px) {
          font-size: rem(16);
          line-height: rem(19);
        }
      }
    }
  }
}
