.modal {
  position: relative;

  &__overlay {
    z-index: 100;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.6);
  }


  &__container {
    max-width: 830px;
    max-height: 100vh;
    padding: rem(110) rem(90) rem(85);
    border: rem(6) solid var(--lightblue-color);
    border-radius: rem(24);
    background-color: #C3E6F5;
    overflow-y: auto;
    box-sizing: border-box;

    @media (max-width: 767px) {
      max-width: calc(100vw - 15%);
      padding: rem(50) rem(25);
      border: 0;
    }

    @media (max-width: 574px) {
      max-width: calc(100vw - 30px);
    }
  }

  &__header {
    text-align: center;
  }

  &__title {
    margin-top: 0;
    margin-bottom: rem(70);
    font-family: "Neometric Alt", sans-serif;
    font-size: rem(45);
    font-weight: 600;
    line-height: 1.1;
    color: var(--black-color);
    box-sizing: border-box;

    @media (max-width: 767px) {
      margin-bottom: rem(25);
      font-size: rem(22);
    }
  }

  &__close {
    cursor: pointer;
    position: absolute;
    top: rem(40);
    right: rem(40);
    font-size: rem(28);
    color: var(--lightblue-color);
    background: transparent;
    border: 0;
    transition: all 0.5s ease-in-out;

    @media (max-width: 991px) {
      top: rem(22);
      right: rem(25);
    }

    &::before {
      content: "\2715";
    }

    &:hover {
      opacity: 0.8;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
  }

  &__form {
    &-label {
      margin-bottom: rem(25);

      @media (max-width: 767px) {
        margin-bottom: rem(10);
      }
    }

    &-input {
      width: 100%;
      height: rem(98);
      padding-right: rem(45);
      padding-left: rem(45);
      font-size: rem(25);
      line-height: rem(98);
      color: var(--black-color);
      background: var(--white-color);
      border: rem(3) solid var(--lightblue-color);
      border-radius: rem(62);
      transition: all 0.5s ease-in-out;
      box-sizing: border-box;
      outline: none;

      @media (max-width: 767px) {
        height: rem(50);
        padding-right: rem(25);
        padding-left: rem(25);
        font-size: rem(16);
        line-height: rem(50);
      }

      &::placeholder {
        color: #51b5e3;
      }

      &.error, &:active, &:focus {
        border-color: var(--black-color);
      }
    }
  }

  &__btn {
    cursor: pointer;
    width: 100%;
    height: rem(110);
    margin-bottom: rem(58);
    font-family: "Neometric Alt", sans-serif;
    font-size: rem(25);
    font-weight: 600;
    line-height: rem(110);
    text-align: center;
    text-transform: uppercase;
    color: var(--white-color);
    background: #171664;
    border: 0;
    border-radius: rem(100);
    transition: all 0.5s ease-in-out;

    @media (max-width: 767px) {
      height: rem(50);
      margin-bottom: rem(24);
      font-size: rem(14);
      line-height: rem(50);
    }

    &:hover {
      background-color: #3a397b;
    }
  }

  &__legal {
    font-size: rem(25);
    line-height: rem(30);
    color: var(--black-color);

    @media (max-width: 767px) {
      font-size: rem(14);
      line-height: rem(17);
    }

    a {
      color: #171664;
    }
  }
}

#modal-review-text {
  .modal {
    &__container {
      max-width: rem(547);
      padding: rem(30) rem(50);
      background-color: var(--white-color);

      @media (max-width: 767px) {
        max-width: calc(100vw - 15%);
        padding: rem(30) rem(30);
        border: 0;
      }

      @media (max-width: 574px) {
        max-width: calc(100vw - 30px);
      }
    }

    &__close {
      top: rem(25);
      right: rem(25);
    }

    &-review {
      &__author {
        display: flex;
        align-items: flex-end;
      }

      &__photo {
        width: rem(67);
        height: rem(67);
        margin-right: rem(12);
        border-radius: 50%;
      }

      &__name {
        margin: 0;
        font-family: 'Proxima Nova', sans-serif;
        font-size: rem(20);
        font-weight: 700;
        line-height: 1;
        line-height: rem(24);
      }
      &__insta {
        cursor: pointer;
        display: inline-block;
        width: rem(35);
        height: rem(35);
        background: var(--lightblue-color);
        transition: 0.2s;
        background-size: contain;
        -webkit-mask-image: url(../img/svg/insta.svg);
        mask-image:url(../img/svg/insta.svg);

        @media(max-width: 1179px) {
          left: rem(24);
        }

        &:hover {
          background: #36A753;
        }
      }
    }
  }
}

@keyframes mmfadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes mmfadeOut {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes mmslideIn {
  from {
    transform: translateY(15%);
  }

  to {
    transform: translateY(0);
  }
}

@keyframes mmslideOut {
  from {
    transform: translateY(0);
  }

  to {
    transform: translateY(-10%);
  }
}

.micromodal-slide {
  display: none;

  &.is-open {
    display: block;
  }

  &[aria-hidden="false"] {
    .modal__overlay {
      animation: mmfadeIn 0.3s cubic-bezier(0, 0, 0.2, 1);
    }

    .modal__container {
      animation: mmslideIn 0.3s cubic-bezier(0, 0, 0.2, 1);
    }
  }

  &[aria-hidden="true"] {
    .modal__overlay {
      animation: mmfadeOut 0.3s cubic-bezier(0, 0, 0.2, 1);
    }

    .modal__container {
      animation: mmslideOut 0.3s cubic-bezier(0, 0, 0.2, 1);
    }
  }

  .modal__container, .modal__overlay {
    will-change: transform;
  }
}